body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Style from MainPage */
html {
	--fontstack-system :-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
	--fontstack-arial : Arial Black, Arial Bold, Gadget, sans-serif;
	--fontstack-arial-narrow :  Arial Narrow, Arial, sans-serif;
}


body {
	font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
	font-family: var(--fontstack-system);
}

footer {
  font-size: .7rem;
}


header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 1rem;

}

.yt-icon  {
  height: 75px;
  transition: height .5s;
  margin-left: 20px;
  margin-right: 20px;
}

.logoText {
  font-size: 3rem;
  transition: font-size .5s;
  margin: 0;
  font-weight: bold;
  color: #FF0000;
}

@media (max-width: 760px) {

  .yt-icon {
    height: 45px;
    margin-left: 7px;
    margin-right: 7px;
  }

  .logoText {
    font-size: 2rem;
  }
}

@media (max-width: 425px) {

  .yt-icon {
    height: 24px;
    margin-left: 6px;
    margin-right: 6px;
  }

  .logoText {
    font-size: 1rem;
  }
}

.vbuttonContainer{
  width: 126px;
  transition: width .5s;
  position: relative;
  margin: .25rem;
}

.removeButton{
  color: red;
  position: absolute;
  top: 0;
  right: 0;
}

.videoButton {
  cursor: pointer;
  border-radius: 50%;
  overflow: hidden;
  border-style: solid;
  width: 126px;
  max-width: 100%;
}
.videoButton > img {
  max-width: 100%;
}

@media (max-width: 425px) {
  .vbuttonContainer {
    margin: 0rem;
    width: 100px;
  }
  .videoButton {
    width: 100px;
    max-width: 100%;
  }
}


.buttonDisplay {
  display: flex;
  flex-direction:row;
  flex-wrap: wrap;
  justify-content: center;
}


#youtubePlayer {
  max-width: 99vw;
}

.videoplayer {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  display: none;
  opacity: 0;
  transition: opacity .5s;
  background-color: rgba(100,100,100,.5);
}

.visible {
  display: block;
  opacity: 100;
}



.addButtonModal {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  display: none;
  opacity: 0;
  transition: opacity .5s;
  background-color: rgba(100,100,100,1);
}

.visible {
  display: block;
  opacity: 100;
}

